<template>
  <div class="hotsong">
    <div class="headimg">
      <img src="./../../assets/img/hotsongbg.png" />
      <p>
        更新日期：{{
          new Date(
            new Date().setTime(this.playlist.updateTime)
          ).toLocaleDateString()
        }}
      </p>
    </div>
    <div @click="play(item.id)" class="list" v-for="(item, index) in newsonglist" :key="index +item.id">
      <Musiclist :id="item.id" :songname="item.name" :authorarr="item.ar">
        <div style="width:2em" :class="item.rank > 2 ? 'silver' : 'red'">
          {{ item.rank + 1 > 9 ? item.rank + 1 : "0" + (1 + item.rank) }}
        </div>
      </Musiclist>
    </div>
    <div style="text-align:center" v-if="newsonglist.length == 0">
        <i style="color:skyblue;font-size:30px" class="fa fa-spinner fa-pulse"></i>
      </div>
    <div v-if="more" @click="add" class="more"><span class="fa fa-send"></span> 更多</div>
  </div>
</template>

<script>
import Musiclist from "@/components/Musiclist.vue";
export default {
  data() {
    return {
      playlist: {},
      // 装载歌曲列表
      songlist: [],
      sum:0,
      total:0,
      more:false,
    };
  },
  methods: {
      play:function(id){
          // this.$router.push("/songplay/"+id)
           console.log('songlist onclick');
      this.$root.$children[0].playlist= this.newsonglist;
      this.$root.$children[0].currentPlayId = id;
      },
      add:function(){
        let data = this.playlist.trackIds;
        let max = 0;
        if(this.sum + 20 >= this.total){
          max = this.total;
          this.more = false;
        }else{
           max = this.sum + 20;
           this.more = true;
        }
        for (let i = this.sum; i < max; i++) {
          this.$axios(`/song/detail?ids=${data[i].id}`).then((data) => {
            let obj = data.data.songs[0];
            obj.rank = i;
            obj.song = {};
              // obj.song.artists = [];
              obj.song.artists = data.data.songs[0].ar;
            this.songlist.push(obj);
          });
          
        }
        this.sum += 20;
      }
  }
  ,
  computed: {
    newsonglist: function () {
      return this.songlist.sort((a, b) => {
        return a.rank - b.rank;
      });
    },
  },
  components: {
    Musiclist,
  },
  created() {
    
    this.$axios("/playlist/detail?id=3778678")
      .then((data) => {
        this.playlist = data.data.playlist;
        return data.data.playlist.trackIds;
      })
      .then((data) => {
        this.total = data.length;
        this.add();
      });
  },
};
</script>
<style lang="scss" scoped>
.hotsong {
  height: 100%;
    > .list{
        .red {
        color: rgb(223, 52, 144);
        }
        .silver{
            color: darkblue;
        }

    }
  
  .headimg {
      margin-bottom: 10px;
    position: relative;
    img {
      width: 100vw;
      display: block;
      border-radius: 20px;
    }
    p {
      color: white;
      font-size: 10px;
      position: absolute;
      left: 18px;
      top: 80%;
    }
  }
  .more{
    text-align: center;
    font-style: italic;
    color: darkblue;
  }
}
</style>