<template>
  <div class="songplay">
    <!-- 定义一小部分的播放器 -->
    <div class="halfview" v-show="!$root.$children[0].fullView">
      <div class="img" @click="$root.$children[0].fullView = true">
        <img :src="detail.al.picUrl" />
      </div>
      <div class="word" @click="$root.$children[0].fullView = true">
        {{ detail.name + "-" + author }}
      </div>
      <div class="play" @click="play = !play">
        <div v-show="!play" class="fa fa-play"></div>
        <div v-show="play" class="fa fa-pause"></div>
        <canvas
          style="display: block"
          id="canvas"
          width="40"
          height="40"
        ></canvas>
      </div>
    </div>
    <!-- 定义全屏播放器 -->
    <div
      v-show="$root.$children[0].fullView"
      class="fullview"
      style="background-color: skyblue"
    >
    <div class="bg"
    :style="`background-image:url(${detail.al.picUrl})`"
    ></div>

      <!-- 定义播放列表 -->
      <div class="list" :class="{ listshow: listshow }">
        <div class="listhid" @click="listshow = false">退出</div>
        <div>
          <div
          @click="$root.$children[0].currentPlayId=item.id"
            :class="{ white: item.name == detail.name }"
            v-for="(item, index) in playlist"
            :key="index + new Date().getTime()"
          >
            <Musiclist
              :songname="item.name"
              :authorarr="item.song.artists"
              :id="item.id"
            ></Musiclist>
          </div>
        </div>
      </div>
      
      <!-- 返回和评论按钮 -->
      <div class="head">
        <div class="back fa-close fa" @click="back"></div>
        <div class="comment" @click="comment(detail.id)">
          评论<span class="fa fa-commenting"></span>
        </div>
      </div>
      <!-- 歌曲名字和歌手 -->
      <div class="brief">
        <div class="name">{{ detail.name }}</div>
        <div class="author">{{ author }}</div>
      </div>
      <!-- 播放器和歌词 -->
      <div class="fix">
        <div class="rotate" v-show="playshow" @click="controlplay">
          <img :class="{ pause: !play }" :src="detail.al.picUrl" alt="" />
          <div v-show="!play" class="fa fa-play"></div>
          <div v-show="play" class="fa fa-pause"></div>
        </div>
        <div style="height: 10px"></div>
        <div class="lyric" @click="playshow = !playshow">
          <div
            :class="{ red: time == item.time }"
            v-for="(item, index) in lyricarr"
            ref="list"
            :data-time="item.time"
            :key="index"
          >
            {{ item.lyc }}
          </div>
        </div>
      </div>
      <!-- 播放进度条 -->
      <div class="play">
        <div class="bar">
          <div class="current">
            {{ parseInt(current / 60) }}:{{ current % 60 }}
          </div>
          <div ref="pro" class="progress" @click="changeprogress">
            <div ref="abc" ></div>
          </div>
          <div class="total">{{ parseInt(total / 60) }}:{{ total % 60 }}</div>
        </div>
        <div class="toggle">
          
          <div @click="toggle(-1)" class="fa fa-step-backward"></div>
          <div @click="playorder" class="fa fa-step-forward"></div>
          <div class="fa fa-align-left" @click="listshow = true"></div>
          <div @click="order = !order" :class="{'fa-random':!order,'fa-retweet':order}" class="fa "></div>
          <div v-if="$root.$children[0].isCollect" class="col fa fa-heart" @click="del_col"></div>
          <div  v-if="!$root.$children[0].isCollect" class="col fa fa-heart-o" @click="add_col"></div>
        </div>
      </div>
    </div>
    <audio
    autoplay
      ref="audio"
      @ended="playorder"
      @timeupdate="audioplay"
      @playing="play = true"
       :src="`https://music.163.com/song/media/outer/url?id=${detail.id}.mp3`"
    ></audio>
                                
  </div>
</template>

<script>


import Musiclist from "./Musiclist";
export default {
  props: ["fullview", "currentplayid", "playlist"],
  components: {
    Musiclist,
  },
  data() {
    return {
      // 定义列表是否显示
      listshow: false,
      // 当前歌曲在播放列表的下标
      index: 0,
      // 当前时间 总时间
      total: 1,
      current: 1,
      // 播放暂停开关
      play: false,
      // 播放到哪儿的显示红色
      time: "0",
      detail: {
        name: "起风了",
        ar: [{name:'买辣椒也用卷'}],
        al: {
          picUrl: "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4068622438,691504340&fm=26&gp=0.jpg",
        },
        id: "1330348068",
      },
      // 歌词李彪
      lyricarr: [{"time":0,"lyc":"作词 : 米果"},{"time":0.697,"lyc":"作曲 : 高橋優"},{"time":1.394,"lyc":"编曲：池窪浩一 (Kouichi Ikekubo)"},{"time":3.742,"lyc":""},{"time":26.137,"lyc":"这一路上走走停停"},{"time":29.192,"lyc":"顺着少年漂流的痕迹"},{"time":32.281,"lyc":"迈出车站的前一刻"},{"time":35.395,"lyc":"竟有些犹豫"},{"time":38.394,"lyc":"不禁笑这近乡情怯"},{"time":41.791,"lyc":"仍无可避免"},{"time":44.419,"lyc":"而长野的天"},{"time":45.734,"lyc":"依旧那么暖"},{"time":47.284,"lyc":"风吹起了从前"},{"time":50.053,"lyc":"从前初识这世间"},{"time":52.785,"lyc":"万般流连"},{"time":54.562,"lyc":"看着天边似在眼前"},{"time":57.698,"lyc":"也甘愿赴汤蹈火去走它一遍"},{"time":62.57,"lyc":"如今走过这世间"},{"time":65.605,"lyc":"万般流连"},{"time":67.236,"lyc":"翻过岁月不同侧脸"},{"time":70.466,"lyc":"措不及防闯入你的笑颜"},{"time":76.113,"lyc":"我曾难自拔于世界之大"},{"time":80.207,"lyc":"也沉溺于其中梦话"},{"time":83.441,"lyc":"不得真假 不做挣扎 不惧笑话"},{"time":89.101,"lyc":"我曾将青春翻涌成她"},{"time":92.53,"lyc":"也曾指尖弹出盛夏"},{"time":95.881,"lyc":"心之所动 且就随缘去吧"},{"time":102.357,"lyc":"逆着光行走 任风吹雨打"},{"time":118.63,"lyc":"短短的路走走停停"},{"time":121.771,"lyc":"也有了几分的距离"},{"time":124.841,"lyc":"不知抚摸的是故事 还是段心情"},{"time":131.318,"lyc":"也许期待的不过是 与时间为敌"},{"time":137.098,"lyc":"再次看到你"},{"time":138.594,"lyc":"微凉晨光里"},{"time":140.161,"lyc":"笑得很甜蜜"},{"time":142.723,"lyc":"从前初识这世间"},{"time":145.542,"lyc":"万般流连"},{"time":147.27,"lyc":"看着天边似在眼前"},{"time":150.22,"lyc":"也甘愿赴汤蹈火去走它一遍"},{"time":155.313,"lyc":"如今走过这世间"},{"time":158.262,"lyc":"万般流连"},{"time":159.99099999999999,"lyc":"翻过岁月不同侧脸"},{"time":162.962,"lyc":"措不及防闯入你的笑颜"},{"time":168.87,"lyc":"我曾难自拔于世界之大"},{"time":172.942,"lyc":"也沉溺于其中梦话"},{"time":176.145,"lyc":"不得真假 不做挣扎 不惧笑话"},{"time":181.742,"lyc":"我曾将青春翻涌成她"},{"time":185.839,"lyc":"也曾指尖弹出盛夏"},{"time":188.669,"lyc":"心之所动 且就随缘去吧"},{"time":220.529,"lyc":"晚风吹起你鬓间的白发"},{"time":224.178,"lyc":"抚平回忆留下的疤"},{"time":227.391,"lyc":"你的眼中 明暗交杂 一笑生花"},{"time":232.9,"lyc":"暮色遮住你蹒跚的步伐"},{"time":236.93200000000002,"lyc":"走进床头藏起的画"},{"time":240.146,"lyc":"画中的你 低着头说话"},{"time":245.68,"lyc":"我仍感叹于世界之大"},{"time":249.391,"lyc":"也沉醉于儿时情话"},{"time":252.756,"lyc":"不剩真假 不做挣扎 无谓笑话"},{"time":258.529,"lyc":"我终将青春还给了她"},{"time":262.281,"lyc":"连同指尖弹出的盛夏"},{"time":265.609,"lyc":"心之所动 就随风去了"},{"time":271.204,"lyc":"以爱之名 你还愿意吗"},{"time":279.145,"lyc":""},{"time":279.98199999999997,"lyc":"吉他： 池窪浩一 (Kouichi Ikekubo)"},{"time":280.994,"lyc":"贝斯：小島剛広 (Takehiro Kojima)"},{"time":281.661,"lyc":"鼓：吉田佳史 (TRICERATOPS) (Yoshifumi Yoshida(TRICERATOPS))"},{"time":282.463,"lyc":"键盘：平畑徹也 (Tetsuya Hirahata)"},{"time":283.341,"lyc":"小提琴：須磨和声 (Wasei Suma)"},{"time":284.211,"lyc":"弦乐：須磨和声、池窪浩一"},{"time":285.258,"lyc":""},{"time":286.105,"lyc":"录音工程：玉乃井光紀 (Mitsunori Tamanoi) - studioFine"},{"time":286.76,"lyc":"发行公司：Negia Entertainment Inc."},{"time":287.476,"lyc":"特别支持：中村光雄(Mitsuo Nakamura)"},{"time":291.019,"lyc":""}],
      // 转圈圈是否显示
      playshow: true,
      // 是否按顺序播放  默认true
      order:true
    };
  },

  computed: {
    // 作者显示 可能不止一个人
    author: function () {
      return this.detail.ar
        .map((item, index) => {
          return item.name;
        })
        .join(" / ");
    },
  },
  // 创建的时候使用这个方法
  created() {},
  mounted() {
    // 这个方法是定义歌曲的当前时间和总时间
    this.$refs.audio.addEventListener("canplay", () => {
      this.current = parseInt(this.$refs.audio.currentTime);
      this.total = isNaN(parseInt(this.$refs.audio.duration))
        ? 0
        : parseInt(this.$refs.audio.duration);
    });
    this.circleplan(document.getElementById("canvas"), 0, "skyblue", "black");
  },
  // 路由发生变化的使用这个方法
  watch: {
    // 点击按钮让它播放或者暂停
    play: function () {
      this.$root.$children[0].isPlay = this.play;
      if (this.play) {
        this.$refs.audio.play();
        // this.$root.children[0]
        
      } else {
        this.$refs.audio.pause();
      }
    },
    currentplayid: function () {
      this.setdata();
      for (let i = 0; i < this.playlist.length; i++) {
        if (this.currentplayid == this.playlist[i].id) {
          this.index = i;
          break;
        }
      }
      this.$root.$children[0].historyList.push(this.currentplayid)
      
    },
  },
  // 创建的时候使用这个方法
  methods: {
    add_col(){
     this.$root.$children[0].add_collect(this.$root.$children[0].currentPlayId)
    },
    del_col(){
this.$root.$children[0].del_collect(this.$root.$children[0].currentPlayId)
    },
    circleplan: function (ele, percen, bgco, cirleco) {
      let pi = 2 * Math.PI;
      let con = ele.getContext("2d");
      // 圆心
      let cx = ele.width / 2;
      let cy = ele.height / 2;

      // 先画背景，再画圆
      con.save();
      con.fillStyle = bgco;
      con.fillRect(0, 0, ele.width, ele.height);
      con.restore();
      con.fillStyle = "white";
      con.arc(cx, cy, 20, 0, pi);
      con.fill();
      con.save();
      con.beginPath();

      con.fillStyle = cirleco;
      con.lineTo(cx, cy);
      con.arc(cx, cy, 20, 0 - pi / 4, (pi / 100) * percen - pi / 4);
      con.closePath();
      con.fill();
      con.restore();

      con.save();
      con.beginPath();
      con.fillStyle = bgco;
      con.arc(cx, cy, 15, 0, pi);
      con.fill();
      con.closePath();
      con.restore();
    },
    setdata: function () {
      // "/song/detail?ids=" 包含歌曲信息 歌名 歌手 封面
      //  https://music.163.com/song/media/outer/url?id=id.mp3 播放url
      // /lyric?id=33894312
      // 获取当前播放的歌曲详情
      let id = this.currentplayid;
      this.$axios("/song/detail?ids=" + id).then((data) => {
        this.detail = data.data.songs[0];
      });
      // 先去除歌词
      this.lyricarr = [];
      // 获取歌词
      this.$axios("http://music.kele8.cn/lyric?id=" + id).then(
      // this.$axios("/lyric?id=" + id).then(
        (data) => {
          // console.log(data);
         
          if (data.data.nolyric) {
            this.lyricarr.push({
              time:0,
              lyc:"啊哦哦，此歌曲无歌词"
            })
            return false;
          }
          let lyricarr = data.data.lrc.lyric
            .split("\n")
            .filter((item, index) => {
              return item.length > 0;
            });
            if( ! lyricarr[0].includes("[")){
              console.log(lyricarr.map(v =>{
                  let aaaaa = v+"\n\t";
                  return aaaaa;
                }).toString());
              this.lyricarr.push({
                time:0,
                lyc:lyricarr.map(v =>{
                  let aaaaa = v+"\n";
                  return aaaaa;
                }).toString()
              })
              
              return false;
            }
          lyricarr.forEach((item, index) => {
            let time = item
              .match(/\[.*?\]/g)[0]
              .trim()
              .slice(1, -1)
              .split(":");
            time = time.map((item2) => {
              return parseFloat(item2);
            });
            time = time[0] * 60 + time[1];
            let lyc = item.match(/\].*/g)[0].slice(1).trim();
            this.lyricarr.push({
              time,
              lyc,
            });
          });
        }
      );
    },
    // 按下进度条播放到哪里
    changeprogress: function (e) {
     
      let pro = this.$refs.pro;
      let left = pro.getBoundingClientRect().left;
      let width = pro.getBoundingClientRect().width;
      let x = e.clientX;
      let a = x - left;
      let progress = parseInt((a / width) * 100);
      progress = (progress * this.total) / 100;
      this.$refs.audio.currentTime = progress;
      this.play = true;
    },
    // 当歌曲播放的时候 进度条滑到哪里
    audioplay: function (e) {
      let c = document.getElementById("canvas");
      this.circleplan(c, 0, "skyblue", "black");
      if (this.$refs.list) {
        let abc = this.$refs.abc;
        this.current = parseInt(e.target.currentTime);
        this.total = isNaN(parseInt(e.target.duration))
          ? 0
          : parseInt(e.target.duration);
        if (abc) {
          abc.style.width = (this.current / this.total) * 100 + "%";
          
        }
        this.circleplan(
          c,
          (this.current / this.total) * 100,
          "skyblue",
          "black"
        );
        let ele = null;
        for (let i = 0; i < this.$refs.list.length; i++) {
          let a, b;
          if (i > 0) {
            a = this.$refs.list[i].dataset.time;
          } else {
            a = -1;
          }
          if (i < this.$refs.list.length - 1) {
            b = this.$refs.list[i + 1].dataset.time;
          } else {
            b = 10000;
          }
          if (e.target.currentTime > a && e.target.currentTime < b) {
            ele = this.$refs.list[i];
            let par = ele.parentElement;
            let top = ele.offsetTop;
            this.time = ele.dataset.time;
            if (this.playshow) {
              par.scrollTo({
                top: top - 50,
                behavior: "smooth",
              });
            } else {
              par.scrollTo({
                top: top - 100,
                behavior: "smooth",
              });
            }
          }
        }
        // console.log(ele);
      }
    },
    // 将播放器换一个样式
    back: function () {
     
        
        this.$root.$children[0].fullView = false;
     
      this.listshow = false;
    },
    comment: function (id) {
      // console.log("comment", id);
      this.$router.push("/comment/music/" + id);
       setTimeout(()=>{
      this.$root.$children[0].fullView = false;
       },1000);
      this.listshow = false;
    },
    controlplay: function () {
      this.play = !this.play;
    },
    toggle: function (go) {
      this.index += go;
      let len = this.playlist.length;
      if (this.index < 0) {
        this.index = len - 1;
        
      }
      if (this.index == len) {
        this.index = 0;
      }
      // console.log(this.index);
      this.$root.$children[0].currentPlayId = this.playlist[this.index].id;
    },
    playorder:function(){
      if(this.order){
        this.toggle(1);
      }else{
        if(Math.random() > 0.5 ){
          if(this.index != this.playlist.length-1){
            // console.log('向下随机播放');
          let len = this.playlist.length - this.index-2;
          let newindex = parseInt(len*Math.random()) + this.index;
          let id = this.playlist[newindex].id;
          this.$root.$children[0].currentPlayId=id;
          }else{
            //  console.log('向上随机播放');
          let newindex = parseInt(this.index * Math.random());
          let id = this.playlist[newindex].id;
          this.$root.$children[0].currentPlayId=id;
          }
          
        }else{
          if(this.index != 0){
            // console.log('向上随机播放');
          let newindex = parseInt(this.index * Math.random());
          let id = this.playlist[newindex].id;
          this.$root.$children[0].currentPlayId=id;
          }else{
              // console.log('向下随机播放');
          let len = this.playlist.length - this.index-2;
          let newindex = parseInt(len*Math.random()) + this.index;
          let id = this.playlist[newindex].id;
          this.$root.$children[0].currentPlayId=id;
          }
          
        }
      }
    }
  },
};
</script>
<style lang="scss">
.fullview {
  .bg{
    height: 100vh;
    width: 100vw;
    position: fixed;
    left: 0;
    right: 0;
    z-index: 0;
    filter: blur(50px);
    background-size: 100% 100%;

  }
  .listshow {
    transform: translateY(0) !important;
  }
  .list {
    div:nth-child(2){
      flex: 1;
      overflow: auto;
    }
    display: flex;
    flex-direction: column;
    position: relative;
    transition: all 0.5s;
    .listhid {
     
      width: 100vw;
      text-align: center;
      color: white;
      padding: 5px 0px;
      background-color: deepskyblue;
    }
    .white {
      color: pink;
    }
    height: 50vh;
    box-sizing: border-box;
    position: fixed;
    width: 100vw;
    background-color: skyblue;
    z-index: 4;
    transform: translateY(50vh);
    bottom: 0;
    
    .play {
      top: 0;
      color: black;
    }
  }

  width: 100vw;
  height: 100vh;
  background: {
    size: 100% 100%;
    // position: 450% 450%;
  }
  .head {
    position: relative;
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    padding: 20px 40px;
    .back {
      font-size: 20px;
      color: dodgerblue;
    }
    .comment {
      color: yellowgreen;
    }
  }
  .brief {
    position: relative;
    .name,
    .author {
      text-align: center;
      color: whitesmoke;
    }
    .name {
      font-weight: bold;
    }
    .author {
      font-size: 12px;
      font-weight: 300;
      margin: 20px;
    }
  }
  .fix {
    height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    .rotate {
      height: 60%;
      display: flex;
      justify-content: center;
      position: relative;
      img {
        box-sizing: border-box;
        height: 100%;
        display: block;
        border-radius: 50%;
        border: 10px solid darkgreen;
        animation: xuanzhuan 10s infinite linear;
      }
      .pause {
        animation-play-state: paused;
      }
      > div {
        position: absolute;
        color: white;
        left: 50%;
        top: 50%;
        font-size: 26px;
        transform: translateX(-50%) translateY(-50%);
      }
      > div::after {
        position: absolute;
        display: block;
        content: "";
        width: 50px;
        height: 50px;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 50%;
        left: -13px;
        top: -13px;
      }
    }
    .lyric {
      box-sizing: border-box;
      width: 80%;

      background-color: rgba(0, 0, 0, 0.7);
      border-radius: 20px;
      position: relative;
      flex: 1;
      overflow: auto;
      color: white;
      padding-top: 10px;
      > div {
        text-align: center;
        font-size: 10px;
        margin-bottom: 15px;
      }
      .red {
        color: goldenrod;
      }
    }
  }
  .play {
    position: relative;
    .toggle {
      text-align: center;
      div {
        font-size: 25px;
        color: chartreuse;
      }
      div:nth-child(2) {
        margin: 0 30px;
      }
      div:nth-child(3) {
        color: chartreuse;
      }
      div:nth-child(4) {
        margin: 0 30px;
      }
    }
    background-color: whitesmoke;
    margin-top: 30px;
    border-radius: 15px;
    .bar {
      display: flex;
      align-items: center;

      .current,
      .total {
        width: 4em;
        text-align: center;
        font-weight: bold;
        color: black;
      }
      .progress {
        flex: 1;
        height: 5px;
        border-radius: 2.5px;
        background-color: royalblue;
        position: relative;
        > div {
          position: absolute;
          height: 100%;
          width: 0%;
          background-color: black;
        }
      }
    }
  }
}
.halfview {
  box-sizing: border-box;

  margin: {
