<template>
  <div class="musiclist">
    <div class="rank">
      <slot></slot>
    </div>
    <div class="right">
      <div class="music">
        <div class="name" v-html="name"></div>
        <div class="author">{{ author }}</div>
      </div>
      <span  class="play fa "
      :class="$root.$children[0].currentPlayId == id && $root.$children[0].isPlay ? `fa-pause-circle` : `fa-play-circle`"
      ></span>
    </div>
  </div>
</template>
<script>
export default {
  props: ["songname", "authorarr","keyword","id"],
data () {
  return {
    name: '',  
  }
},

  mounted () {
      this.datachange();
  },
  updated () {
     this.datachange();
  }
  ,
  methods: {
    datachange:function(){
      let str  = this.songname;
      if(this.keyword){
       
        this.name = str.replace(this.keyword,`<span style="color:red">${this.keyword}</span>`)
      }else{
        this.name = str;
       
      }
    }
  },
  computed: {
    author: function () {
      return this.authorarr
        .map((item) => {
          return item.name;
        })
        .join(" / ");
    },
  },
};
</script>



<style lang="scss" scoed>
.musiclist {
  width: 100%;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  > .right {
    width: 100%;
    position: relative;
    flex: 1;
    box-sizing: border-box;
    padding-left: 10px;
    > .music {
      width: 75vw ;
      > .name {
        font-size: 14px;
         table-layout: fixed;
        display: inline-block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 90%;
      }
      > .author {
        width: 50%;
        font-size: 10px;
        table-layout: fixed;
        display: inline-block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    > .play {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
</style>