<template>
  <div class="home">
    <div class="nav">
      <div>
        <img src="./../assets/img/logo.png" />
        <span>wyy</span>
      </div>
      <div @click="clear">
        <router-link to="/">推荐</router-link>
        <router-link to="/hotsonglist">热歌</router-link>
      </div>

      <div>
        <router-link to="/search">
          <input
            @keydown.enter="search"
            type="text"
            v-model="searchval"
            placeholder="搜索"
          />
        </router-link>
      </div>
    </div>

    <div class="body">
      <transition
      mode="out-in" 
      enter-active-class="animate__animated animate__fadeInRight"
      leave-active-class="animate__animated animate__fadeOutLeft"
            :duration="500"
      >
        <keep-alive>
          <router-view :updatestore="updatestore" :correlate="searchval" :history="history"></router-view>
        </keep-alive>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchval: "",
      // 搜索历史
      history:[],
    };
  },
  created () {
    this.save();
    
  },
  
  methods: {
    search: function (e) {
     
       this.$router.push("/Searchlist/"+this.searchval);
      //  console.log(e);
      e.target.blur();
      // console.log(this.searchval);
      // 获取
      this.save();
      

    },
    save:function(){
          let history = window.localStorage.getItem("huntingshadowhistory"); //字符串
          // console.log(history);
          // history = JSON.parse()
          history = JSON.parse(history);  //转换为数组
          
          if(history == null){
            history = [];
          }
          if(this.searchval.length > 0  ){
              history.forEach((v,i)=>{
              if(v == this.searchval){
               
                history.splice(i,1)
              }
            })

            history.push(this.searchval)
            }
          this.history = history;
          // 写入
          // console.log(history);
          window.localStorage.setItem("huntingshadowhistory",JSON.stringify(history))
    },
    updatestore:function(){
          let history = window.localStorage.getItem("huntingshadowhistory"); //字符串
          // console.log(history);
          // history = JSON.parse()
          history = JSON.parse(history);  //转换为数组    
          if(history == null){
            history = [];
          }
          this.history = history;
    },
    clear: function () {
      // console.log(33);
      this.searchval = "";
    },
  },
  components: {},
};
</script>


<style  lang='scss'>
.home {
  .body {
    background-color: silver;
    // background-color: violet;
    height: 100%;
    box-sizing: border-box;
    padding-top: 140px;
    padding-bottom: 50px;
    > div {
       overflow: auto;
      width: 90%;
      margin-left: 5%;
    }
  }

  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  > .nav {
    background-color: white;
    a {
      text-decoration: none;
      color: silver;
    }
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    z-index: 1;

    > div:nth-child(1) {
      box-sizing: border-box;

      background-color: skyblue;
      display: flex;
      height: 60px;
      align-items: center;
      img {
        border-radius: 50%;
        height: 25px;
        width: 25px;
        margin: 0px 10px;
      }
      span {
        text-shadow: -1px -1px 1px #000, 1px 1px 1px #fff;
        color: whitesmoke;
      }
    }

    > div:nth-child(2) {
      margin: 5px 0;
      display: flex;
      justify-content: space-around;

      > a {
        display: block;
        padding-bottom: 5px;
        border: {
          bottom: 3px solid transparent;
        }
        &.router-link-exact-active {
          color: skyblue;
          font-weight: bold;

          border-color: skyblue;
        }
      }
    }

    > div:nth-child(3) {
      background-color: silver;
      text-align: center;
      padding-top: 5px;
      box-sizing: border-box;
      padding-bottom: 10px;
      > a {
        display: block;
        > input {
          width: 80%;
          padding: 3px 0;
          text-align: center;
          outline: none;

          border: 1px solid silver {
            radius: 11px;
          }
        }
      }
    }
  }
}
</style>