<template>
  <div class="songlist">
    <div style="display: flex; justify-content: center; align-items: center;height:100vh"  v-if="detail && detail.adType == undefined">
        <i style="color:skyblue;font-size:30px;" class="fa fa-spinner fa-pulse"></i>
      </div>
    <div class="brief" v-if="detail && detail.adType != undefined">
      <div @click="back" class="back fa-close fa"></div>
      <div class="img">
        <img :src="detail.coverImgUrl" />
      </div>
      <div class="name">{{ detail.name }}</div>
      <div class="author">
        <img :src="detail.creator.avatarUrl" />
        {{ detail.creator.nickname }}
      </div>
      <div class="playcount">播放量:{{ playcount }}</div>
      <div class="song">
        <div class="count">歌单 共{{ detail.trackIds.length }}首</div>
        <div class="comment" @click="comment(detail.id)">
          评论<span class="fa fa-commenting"></span>
        </div>
      </div>
    </div>

    <div class="list">
      <div
        v-for="(item, index) in newsonglist"
        @click="play(item.id)"
        :key="index + new Date().getTime()"
      >
        <Musiclist :songname="item.name" :authorarr="item.ar">
          {{ index + 1 > 9 ? index + 1 : "0" + (index + 1) }}
        </Musiclist>
      </div>
    </div>
  </div>
</template>


<script>
import Musiclist from "./../components/Musiclist";

export default {
  components: {
    Musiclist,
  },
  data() {
    return {
      // 储存歌单数据
      detail: {
        trackIds:[],
      },
      // 储存歌曲lao数据
      oldsonglist: [],
    };
  },
  // 创建的时候使用这个方法
  // created() {
  //   this.setdata();
  // },
  // 路由发生变化的使用这个方法
  // watch: {
  //   $route: function () {
  //     this.setdata();
  //   },
  // },
  beforeRouteEnter(to, from, next){
    next(vm=>{
      vm.oldsonglist = [];
      vm.setdata();
    })
  },
  computed: {
    newsonglist: function () {
      return this.oldsonglist.sort((a, b) => {
        return a.rank - b.rank;
      });
    },
    playcount: function () {
      let a = 0;
      if (this.detail.playCount > 100000000) {
        // console.log(this.detail.playCount);
        a = (this.detail.playCount / 100000000).toFixed(1) + "亿";
      } else if (this.detail.playCount > 10000) {
        a = (this.detail.playCount / 10000).toFixed(1) + "万";
      } else {
        a = this.detail.playCount;
      }
      return a;
    },
  },
  methods: {
      back:function(){
          this.$router.go(-1);
      }
      ,
    comment: function (id) {
      // console.log("comment id", id);
      this.$router.push("/comment/playlist/"+id)
    },
    play: function (id) {
      // console.log("songid", id);
      // this.$router.push("/songplay/"+id)
      // console.log('songlist onclick');
      this.$root.$children[0].playlist= this.newsonglist;
      this.$root.$children[0].currentPlayId = id;
    },
    setdata: function () {
      let id = this.$route.params.id;
      this.$axios("/playlist/detail?id=" + id).then((data) => {
        this.detail = data.data.playlist;

        for (let i = 0; i < this.detail.trackIds.length; i++) {
          this.$axios("/song/detail?ids=" + this.detail.trackIds[i].id).then(
            (data2) => {
              let obj = data2.data.songs[0];
              obj.rank = i;
              obj.song = {};
              // obj.song.artists = [];
              obj.song.artists = data2.data.songs[0].ar;
              this.oldsonglist.push(obj);
            }
          );
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.songlist {
  position: relative;
  padding-bottom: 50px;
  .back {
    font-size: 25px;
    color: deepskyblue;
    position: absolute;
    left: 20px;
    top: -20px;
    user-select: none;
  }
  .brief {
    .img {
      width: 60%;
      margin: {
        left: 20%;
        top: 40px;
      }
      border-radius: 20px;
      box-shadow: 20px 12px 20px 0px silver;
      overflow: hidden;
      img {
        display: block;
        width: 100%;
        
      }
    }
    .name {
      margin-top: 20px;
      text-align: center;
      width: 50%;
      font-size: 18px;
      margin-left: 25%;
      font-weight: 800;
    }
    .author {
      margin-top: 10px;
      font-weight: 600;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        height: 20px;
        border-radius: 50%;
      }
    }
    .playcount {
      text-align: center;
      font-size: 15px;
      margin-top: 15px;
      color: dimgrey;
    }
    .song {
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      margin: {
        top: 10px;
        bottom: 20px;
      }
      padding: 0 15px;
    }
  }
  .list {
    padding: 0 15px;
    .rank {
      color: dodgerblue;
    }
  }
}
</style>