<template>
  <div class="search">
    <div class="title" v-show="correlate.length == 0">热门搜索</div>
    <div class="hot" v-show="correlate.length == 0">
      <div
        @click="search(item.first)"
        v-for="(item, index) in hot"
        :key="index"
      >
        {{ item.first }}
      </div>
    </div>
    <div class="history" v-show="correlate.length == 0">
      <div class="title">搜索历史</div>
      <div class="list">
        <div v-for="(item2, index1) in history" :key="index1">
          <span @click="search(item2)">{{ item2 }}</span>
          <span @click="abc(index1)" class="fa fa-trash"></span>
        </div>
      </div>
    </div>

    <div class="hint" v-show="correlate.length != 0">搜索"{{ correlate }}"</div>
    <div class="correlatelist" v-show="correlate.length != 0">
      <div
        @click="search(item)"
        v-for="(item, index) in xiangguan"
        :key="index"
      >
        <span class="fa-search fa" style="color: dimgrey"></span>
        {{ item }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["correlate", "history","updatestore"],

  data() {
    return {
      hot: [],
      // 储存相关搜索
      xiangguan: [],
      time:null,  
    };
  },
  watch: {
    correlate: function () {
      this.xiangguan = [];
      // if (this.correlate.trim().length > 0) {
      //       let time = this.time;
      //        this.time = new Date().getTime();
      //     if(new Date().getTime()  > time+ 1000){
      clearTimeout(this.time);
      this.time = setTimeout(()=>{
          this.$axios("/search/suggest?keywords=" + this.correlate).then(
          (data) => {
            let obj = data.data.result;
            for (let key in obj) {
              for (let item of obj[key]) {
                if (item.name) {
                  this.xiangguan.push(item.name);
                }
              }
            }
            this.xiangguan = [...new Set(this.xiangguan)];
          }
        );
      //     }
      },1000);
      

      // }
    },
  },
  created() {
    this.$axios("/search/hot").then((data) => {
      this.hot = data.data.result.hots;
    });
  },
  methods: {
    search: function (name) {
      
      this.$router.push("/Searchlist/" + name);
      let history = window.localStorage.getItem("huntingshadowhistory"); //字符串
          // console.log(history);
          // history = JSON.parse()
          history = JSON.parse(history);  //转换为数组
          
          if(history == null){
            history = [];
          }
          if(name.length > 0 ){
           
            history.forEach((v,i)=>{
              if(v == name){
               
                history.splice(i,1)
              }
            })
            

              history.push(name)
            
          }
          // 写入
          window.localStorage.setItem("huntingshadowhistory",JSON.stringify(history))
          this.updatestore();
    },
    abc: function (index) {
      let history = window.localStorage.getItem("huntingshadowhistory"); //字符串
      history = JSON.parse(history);  //转换为数组
      history.splice(index,1);
      window.localStorage.setItem("huntingshadowhistory",JSON.stringify(history))



      this.updatestore();
  },
  },

  
};
</script>

<style lang="scss" scoped>
.search {
  > .title {
    font-size: 10px;
  }
  > .hot {
    display: flex;
    flex-wrap: wrap;
    > div {
      margin: 5px 15px;
      font-size: 14px;
    }
  }
  > .history {
    .title {
      color: red;
      font-size: 10px;
      margin: {
        top: 10px;
      }
    }
    .list {
      div {
        text-indent: 1em;
        display: flex;
        justify-content: space-between;
      }
    }
  }
  .correlatelist {
    div {
      margin-bottom: 10px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 14px;
    }
  }
  .hint {
    color: blueviolet;
    font-size: 12px;
    margin: 10px 0;
  }
}
</style>