<template>
  <div v-show="appShow" id="app">
   
      
    
    <router-view></router-view>
    <Songplay 
    :fullview="fullView"
    id="songplay"
    :currentplayid="currentPlayId"  
    :playlist="playlist"
      ></Songplay>
      <div v-show="!fullView" class="btn" @click="optionShow = !optionShow">
        <div></div>
      </div>
      <transition
      mode="out-in"
      enter-active-class="animate__animated animate__bounceInRight"
    leave-active-class="animate__animated animate__bounceOutRight"
      >
      
        <div key="nihao" class="option" v-show="optionShow">
        <div class="ls" @click="hist_list">历史</div>
        <div class="sc" @click="collect_list">收藏</div>
      </div>
     
         
      </transition>
     
   </div>
</template>

<script>
import Songplay from './components/Songplay'
export default {
  data () {
    return {
      // 定义音乐播放器是否全屏状态  默认否
      fullView:false,
      // 当前播放id 默认起风了
      currentPlayId:1330348068,
      // 播放列表id 
      playlist:[],
      isPlay:false,
      appShow:false,
      optionShow:false,
      collectList:[],
      historyList:[1330348068],
      // isCollect:false,
    }
  },
  computed:{
    isCollect(){
      let a = false;
      let cur = this.currentPlayId;
      let col = this.collectList;
      for(let i =0 ; i < col.length ;i++){
        if(col[i] == cur){
          a = true
        }
      };
      return a;
    }
  },
  created(){
    let ele1 = document.querySelectorAll(".index_load")[0];
    let ele2 = document.querySelectorAll(".index_load")[1];
    let body = document.querySelector("body")
    setTimeout(()=>{
        document.querySelector("#index_jindu").innerHTML =  "100%";
        setTimeout(()=>{
          body.removeChild(ele1)
          body.removeChild(ele2)
            this.appShow = true;
        },100)
        
    },3000)
  
    // console.log(ele1,ele2,body);
   let a = window.localStorage.getItem("dxp_collect");
   
   if(a == null){
     
   }else{
     this.collectList = JSON.parse(a);
   }
  },




  components: {
    Songplay,
  },
  methods:{
    collect_list(){
      // console.log('co');
      
      if(this.$route.fullPath != "/collect"){
        
        this.$router.push("/collect")
      }
    },
    hist_list(){
        if(this.$route.fullPath != "/history"){
        
          this.$router.push("/history")
      }
    },
    add_collect(id){
      this.collectList.push(id);
      this.save_collect(this.collectList)
    },
    del_collect(id){
      for(let  i=0 ;i<this.collectList.length;i++){
        if(this.collectList[i] == id){
          this.collectList.splice(i,1);
        }
      }
      this.save_collect(this.collectList)
    },
    save_collect(arr){
      window.localStorage.setItem("dxp_collect", JSON.stringify(arr));

    }
  }
}
</script>

