import Vue from 'vue'
import VueRouter from 'vue-router'

// 引入视图
import Home from './../views/Home.vue'
import Hotsonglist from './../views/Home/Hotsonglistview.vue'
import Recommend from './../views/Home/Recommendview.vue'
import Search from './../views/Home/Searchview.vue';
import Searchlist from './../views/Home/Searchlistview.vue';
import Comment from './../views/Comment.vue'

import Songlist from './../views/Songlist.vue'


Vue.use(VueRouter)

const routes = [
  
  {
    path: '/', component: Home,
    children: [
      { path: '/hotsonglist', component: Hotsonglist, name: 'Hotsonglist' ,meta: { title:"热歌榜" } },
      { path: '', component: Recommend, name: 'Recommend' , meta: { title:"推荐" }},
      { path: '/search', component: Search, name: 'Search',meta: { title:"搜索" } },
      { path: '/searchlist/:key', component: Searchlist, name: 'Searchlist',meta: { title:"搜索" } },
    ]
  },
  // 歌单详情
  {
    path:'/songlist/:id',
    component:Songlist,
    name:"Songlist",
    meta: { title:"歌单" }
  },
 
  //评论区
  ,{
    path:'/comment/:type/:id',
    component:Comment,
    name:"comment",
    meta: { title:"评论" }
  }
  ,{
    path:'/collect',
    component:()=>import("./../views/Collect.vue"),
    name:"collect",
    meta: { title:"收藏" }
  }
  ,{
    path:'/history',
    component:()=>import("./../views/History.vue"),
    name:"history",
    meta: { title:"历史" }
  }
  

  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
]

const router = new VueRouter({
  mode: 'hash',
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})
router.beforeEach((to, from, next) => {
  
  document.querySelector("title").innerHTML = to.meta.title;
  next()
  
})


export default router
