<template>
    <div class="searchlist">
        <div v-for="(item,index) in songlistarr" @click="play(item.id)" :key="index+new Date().getTime()">
            <Musiclist :id="item.id" :keyword="$route.params.key" :songname="item.name"  :authorarr="item.artists"></Musiclist>
        </div>
    </div>
</template>
<script>
import Musiclist from './../../components/Musiclist'
export default {
    data () {
        return {
            songlistarr:[],  
            
          }
    },
    created () {
       this.getsonglist();
    },
    beforeRouteEnter:function(form,to,next){
        next(vm=>{
            vm.getsonglist();
        })
    },
    
    methods: {
        play:function(id){
            // this.$router.push("/songplay/"+id)
            this.$root.$children[0].currentPlayId = id;
            // console.log('222');
        },
        getsonglist:function(){
          
                 let key = this.$route.params.key;
            this.$parent.searchval = key ;
            this.$axios("/search?keywords="+key).then(data=>{
               this.songlistarr = data.data.result.songs;
            })
           
           
        }
    },
    components: {
        Musiclist
    }
}
</script>
<style lang="scss">
.searchlist{
    height: 100%;
}
</style>