<template>
  <div class="comment">
    <div class="back">
      <div @click="back" class="back fa-close fa"></div>
    </div>
    <div class="hot">
      <div class="title">热评</div>
      <div class="eg">
        <div
          class="discuss"
          v-for="(item, index) in hot.list"
          :key="index + new Date().getTime()"
        >
          <div class="info">
            <div class="left">
              <img :src="item.user.avatarUrl" />
              <div>
                <p>{{ item.user.nickname }}</p>
                <p>
                  {{ new Date(item.time).getFullYear() }}-{{
                    new Date(item.time).getMonth() + 1
                  }}-{{ new Date(item.time).getDate() }}
                  {{ new Date(item.time).getHours() }}:{{
                    new Date(item.time).getMinutes()
                  }}
                </p>
              </div>
            </div>
            <div class="right">
              {{ item.likedCount }} <span class="fa-thumbs-o-up fa"></span>
            </div>
          </div>
          <div class="rubbishword">
            <span class="reply" v-if="item.beReplied.length > 0">回复
              <span style="color:blue">{{'@'+item.beReplied[0].user.nickname + ':'}}</span> 
            </span>
            {{ item.content }}
            </div>
          <div v-if="item.beReplied.length > 0" class="floor">{{item.beReplied[0].content ? item.beReplied[0].content : '此评论已被删除'  }}</div>
        </div>
      </div>
    </div>
    <div
      class="more"
      v-if="hot.list.length != hot.totallist.length"
      @click="getofsomehot"
    >
      更多
    </div>
    <div class="more" v-if="hot.list.length == hot.totallist.length">
      已经到底了
    </div>

    <div class="ordinary">
      <div class="title" style="color:red">全部评论</div>
      <div class="eg"> 
        <div
          class="discuss"
          v-for="(item, index) in ordinary.list"
          :key="index + new Date().getTime()"
        >
          <div class="info">
            <div class="left">
              <img :src="item.user.avatarUrl" />
              <div>
                <p>{{ item.user.nickname }}</p>
                <p>
                  {{ new Date(item.time).getFullYear() }}-{{
                    new Date(item.time).getMonth() + 1
                  }}-{{ new Date(item.time).getDate() }}
                  {{ new Date(item.time).getHours() }}:{{
                    new Date(item.time).getMinutes()
                  }}
                </p>
              </div>
            </div>
            <div class="right">
              {{ item.likedCount }} <span class="fa-thumbs-o-up fa"></span>
            </div>
          </div>
          <div class="rubbishword">
            <span class="reply" v-if="item.beReplied.length > 0">回复
              <span style="color:blue">{{'@'+item.beReplied[0].user.nickname + ':'}}</span> 
            </span>
            {{ item.content }}
            </div>
          <div v-if="item.beReplied.length > 0" class="floor">{{item.beReplied[0].content ? item.beReplied[0].content : '此评论已被删除'  }}</div>
        
        </div>
       
      </div>
    </div>
    <div
      class="more"
      v-if="ordinary.list.length != ordinary.total"
      @click="getofsomeordinary"
    >
      更多
    </div>
    <div class="more"  v-if="ordinary.list.length == ordinary.total">
      已经到底了
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hot: {
        list: [],
        totallist: [],
      },
      ordinary: {
        list: [],
        total: 0,
      },
    };
  },
  beforeRouteEnter: function (to, from, next) {
    next((vm) => {
      vm.gethotdata();
      vm.getordinarydata();
    });
  },
  beforeRouteUpdate(to, from, next) {
    this.gethotdata();
    this.getordinarydata();
    next();
  },
  methods: {
    back: function () {
      this.$router.go(-1);
    },
    gethotdata: function () {
      // 将所有的热门数据赋值
      let id = this.$route.params.id;
      let type = this.$route.params.type;
      this.$axios(`/comment/${type}?id=${id}&limit=1`).then((data) => {
        this.hot.totallist = data.data.hotComments;
        this.getofsomehot();
      });
    },
    getofsomehot: function () {
      let listlen = this.hot.list.length;
      let totallen = this.hot.totallist.length;
      let quchulen = 0;
      if (listlen + 3 < totallen) {
        quchulen = listlen + 3;
      } else {
        quchulen = totallen;
      }
      this.hot.list = this.hot.list.concat(
        this.hot.totallist.slice(listlen, quchulen)
      );
    },
    getordinarydata: function () {
      // console.log(this.$route);
      let id = this.$route.params.id;
      let type = this.$route.params.type;
      this.$axios(`/comment/${type}?id=${id}&limit=5`).then((data) => {
        
        this.ordinary.total = data.data.total;
        this.ordinary.list = data.data.comments;
      });
    },
    getofsomeordinary: function () {
       let id = this.$route.params.id;
       let type = this.$route.params.type;
      let listlen = this.ordinary.list.length;
      let totallen = this.ordinary.total;
      let quchulen = 0;
      if (listlen + 5 < totallen) {
        quchulen =  5;
      } else {
        quchulen = totallen-listlen;
      }
      this.$axios(`/comment/${type}?id=${id}&limit=${quchulen}&offset=${listlen}`).then((data) => {
        
        data.data.comments.forEach(item=>{
          this.ordinary.list.push(item);
        })
      });
    },
  },
};
</script>

<style lang="scss" scoped >
.comment {
  
  .more{
    margin-bottom: 10px;
    color: violet;
  }
  width: 90%;
  height: 100%;
  box-sizing: border-box;
  padding-bottom: 50px;
  margin-left: 5%;
  overflow: scroll;
  .back {
    padding: 10px 0 15px 0;
    color: darkblue;
    box-sizing: border-box;
    padding-left: 20px;
    font-size: 25px;
    text-shadow: 0 0 5px black;
  }
  > .hot,
  .ordinary {
    .title {
      font-weight: bold;
      font-size: 1.1em;
      text-indent: 0.5em;
      margin-bottom: 10px;
      box-sizing: border-box;
      border-left: skyblue 5px solid;
      text-shadow: 1px 1px 2px green;
    }
    .eg {
      .discuss {
        margin-bottom: 15px;
        .info {
          display: flex;
          justify-content: space-between;
          .left {
            display: flex;

            img {
              display: block;
              width: 30px;
              height: 30px;
              border-radius: 50%;
              margin-right: 10px;
            }
            div {
              p:nth-child(1) {
                font-size: 12px;
                color: steelblue;
              }
              p:nth-child(2) {
                font-size: 10px;
                transform-origin: left;
                transform: scale(0.8);
                color: slategrey;
              }
            }
          }
          .right {
            font-size: 14px;
          }
        }
        .rubbishword {
          margin: 5px 0 4px 0;
          font-size: 14px;
          text-indent: 1em;
        }
        .floor {
          box-sizing: border-box;
          color: whitesmoke;
          font-size: 12px;
          background-color: teal;
          padding: 5px 0;
          border-radius: 10px;
          box-shadow: 1px 1px 2px blue;
          text-indent: 1em;
        }
      }
    }
  }
}
</style>