<template>
  <div class="recommend">
    <div class="recommendlist">
      <div class="title">
        <span class="fa-music fa"></span>
        推荐歌单
      </div>
      <div class="sudoku" >
        <div
          v-for="(item, index) in songlist"
          :key="index"
          @click="details(item.id)"
        >
          <img :src="item.picUrl" />
          <div class="num">
            <span class="fa-headphones fa">{{ playcount[index] }}</span>
          </div>
          <div class="introduce">{{ item.name }}</div>
        </div>
      </div>
      <div style="text-align:center" v-if="songlist.length == 0">
        <i style="color:skyblue;font-size:30px" class="fa fa-spinner fa-pulse"></i>
      </div>
    </div>
    <div class="newmusic">
      <div class="title">
        <span class="fa-music fa"></span>
        最新音乐
      </div>
      <div @click="play(item.id)"  v-for="(item, index) in newsong" :key="index">
        <Musiclist
          :data-id="item.id"
          :songname="item.name"
          :authorarr="item.song.artists"
          :id="item.id"
        >
        </Musiclist>
      </div>
       <div style="text-align:center" v-if="newsong.length == 0">
        <i style="color:skyblue;font-size:30px" class="fa fa-spinner fa-pulse"></i>
      </div>
    </div>
  </div>
</template>

<script>
import Musiclist from "./../../components/Musiclist";

export default {
  data() {
    return {
      songlist: [],
      newsong: [],
    };
  },
  computed: {
    playcount: function () {
      return this.songlist.map((item, index) => {
        let abc = "";
        if (item.playCount > 100000000) {
          abc = (item.playCount / 100000000).toFixed(1) + "亿";
        } else if (item.playCount > 10000) {
          abc = (item.playCount / 10000).toFixed(1) + "万";
        } else {
          abc = item.playcount;
        }
        return abc;
      });
    },
  },
  created() {
   
    this.$axios("/personalized?limit=6").then((data) => {
     
        this.songlist = data.data.result;
      
    });
    this.$axios("/personalized/newsong/limit=30").then((data) => {
        // this.$root.$children[0].playlist=data.data.result;
        // this.$root.$children[0].currentPlayId=data.data.result[0].id;
        this.newsong = data.data.result;
    });
  },
  methods: {
    details: function (id) {
      this.$router.push("/songlist/" + id);
    },
    play: function (id) {
      // console.log("跳转到播放器 ==>" ,id);
      this.$root.$children[0].playlist=this.newsong;
      this.$root.$children[0].currentPlayId = id;

    },
    
  },
  components: {
    Musiclist,
  },
};
</script>

<style lang="scss" scoped>
.recommend {
  height: 100%;
  .title {
    font-weight: bold;
    margin: 20px 0;
  }
  > .recommendlist {
    .sudoku {
      display: flex;
      flex: {
        wrap: wrap;
      }
      justify-content: space-between;
      > div {
        position: relative;

        img {
          border-radius: 8px;
          display: block;
          width: 100%;
        }
        .num {
          position: absolute;
          top: 2px;
          right: 3px;
          font-size: 10px;
          color: whitesmoke;
        }
        .introduce {
          width: 90%;
          margin: 5px 5% {
            bottom: 15px;
          }

          font-size: 12px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        width: 31%;
      }
    }
  }
}
</style>